@import url(https://fonts.googleapis.com/css?family=Raleway:900);
*, html, body {
    box-sizing: border-box;
    margin: 0;
}

section.game-player {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-image: url(/static/media/alchemy.67868075.gif);
    border: 3px solid black;
    overflow: auto;
}

div.grid-canvas-wrapper {
    position: relative;
    margin: 0 auto;
    width: 1000px;
    height: 100%;
    overflow: auto;
    background: rgba(0, 150, 136, 0.13);
}

div.grid-canvas {
    position: relative;
    width: 550px;
    height: 550px;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 75px;
}
div.grid {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgb(219, 241, 220);
    display: grid;
    grid-gap: 1px;
    border: 1px solid gray;
}
div.grid > div.grid-cell {
    width: 100%;
    height: 100%;
    overflow: hidden;
}


.grid-cell {
    background: white;
}

.snake-color-grey {
    background: gray;
}

.snake-color-red {
    background: red;
}

.snake-color-pink {
    background: pink;
}

.snake-color-yellow {
    background: yellow;
}

.snake-color-green {
    background: green;
}


.food-name-apple {
    border: 2px solid red;
    background-image: url(data:image/png;base64,);
    background-size: contain;
}

.food-name-strawberry {
    border: 2px solid pink;
    background-image: url(data:image/png;base64,);
    background-size: contain;
}

.food-name-banana {
    border: 2px solid yellow;
    background-image: url(data:image/png;base64,);
    background-size: contain;
}

.food-name-frog {
    border: 2px solid green;
    background-image: url(data:image/png;base64,);
    background-size: contain;
}


