div.canvas-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(25, 25, 25, 0.51);
    z-index: 5;
    display: grid;
    justify-items: center;
    align-items: center;
}

.dead-text {
    font-size: 35px;
    font-weight: 900;
    font-family: monospace;
    color: white;
}

.button-svg {
    font-family: Raleway, serif;
    font-size: 35px;
}

svg {
    z-index: 2;
    -webkit-filter: drop-shadow(0 0 40px rgba(0, 0, 0, 0.3));
            filter: drop-shadow(0 0 40px rgba(0, 0, 0, 0.3));
}

svg:hover {
    -webkit-filter: drop-shadow(0 0 50px rgba(0, 0, 0, 0.5));
            filter: drop-shadow(0 0 50px rgba(0, 0, 0, 0.5));
}

svg > text {
    font-size: 45px;
}

svg > path {
    cursor: pointer;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;  
    fill: #FFEB3B;
}

.score-board-wrapper {
    height: 75px;
    padding: 10px;
}

.score-board {
    width: 700px;
    height: 100%;
    border: 1px solid #94d695;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    align-items: center;
    justify-items: center;
    font-family: monospace;
    font-weight: 900;
}

.score-board.default {
    background: rgba(76, 175, 80, 0.73);
    color: white;
}

.score-board.white {
    background: white;
}

div.pause-button > button{
    display: inline-block;
    font-size: 20px;
    padding: 5px;
    background: rgb(72, 189, 191);
}

.score-board label {
    font-size: 20px;
    padding: 5px;
}

.score-board span {
    font-size: 22px;
    padding: 10px;

}
.footer {
    position: fixed;
    width: 100%;
    height: 60px;
    text-align: center;
    padding: 10px;
    background: white;
    bottom: 0;
    left: 0;
    z-index: 100;
    font-family: monospace;
    font-weight: 900;
}
